.cont-rev {
   position: relative;
   overflow: hidden;
   width: 100%;
   min-height: 280px;
   margin: 0px 0 80px;
  padding: 0 10% 0px;
 }
.rev{ 
  position: absolute;
  display:inline-block;
  width:80%;
  text-align: center;
  margin: auto;
  top: 105%;
  transition: all .6s ease;
  & article{
    position: relative;
    background-color: lighten($col-rozW, 6) ;
    border: $col-w 3px solid;
    border-radius: 20px;
    box-shadow: $col-roz 0 0 0 2px ,  inset  $col-roz 0 0 0 4px ;
    font-size: 26px;
    font-family: serif;
    padding: 20px 40px;
  }
  .tr{
    position: absolute;
    font-size: 40px;
    right: 100px;
    bottom:-40px;
    color: $col-roz;
  }
}
.rev-h3 {
  text-align: right;
  color: $col-btn;
  & p{
    padding-top: 30px;
    font-size: 18px;
  }
}
.rev-active{
  top: 1%;
}
.cont-dots{
  position: absolute;
  width: 100%;
  display: block;
  bottom: 100px;
  text-align: center;
  .d{
    cursor: pointer;
    display: inline-block;
    height: 20px;
    width: 20px;
    margin: 10px 5px;
    border: $col-roz 4px solid;
    border-radius: 50%; 
    z-index: 5;
  }
  .dots-wrap{
    display: inline-block;
    margin: 0 auto;
  }
}
.dots-active{
  background-color: $col-btn;
}


@media only screen and (max-width:900px) {
  .cont-rev{
   padding: 0 0% 40px;
  }
  .rev{
    width:100%;
  }
}
