.cont-slid {
  margin: 30px 0 100px;
 & div{
    text-align: center;
    margin: 10px 25px 15px;
     & a img{
    margin: 0 auto 20px;
    height: 60px;
    border-bottom: #444444 2px solid;
    border-radius: 5px;
    transition: all .3s ease;
     &:hover{
      transform: scale(1.1);
      box-shadow: 3px 6px 3px #444444 ;
        }
      }
  & a{
    outline : none;
  }
  & span{
    white-space : normal ;
    padding: 20px 0 ;
  }
}}
.slick-initialized .slick-slide{
  max-width: 250px;
  transform: translateX(-20px);
  &.slick-active{
    outline :none ;
  }
}

.slick-dots li button {
  height: 20px;
  width: 20px;
  border: $col-roz 4px solid;
  border-radius: 50%; 
}

.slick-dots .slick-active{
  background-color: $col-btn;
  border-radius: 50%; 
}
