.footer{
  height: 200px;
  width: 100%;
  margin-bottom: -10px;
  color: $col-w;
  background: url('/app/img/a1.jpg') 50% 45% /cover;
} 
.footer-wrap{
  position: absolute;
  height: 200px;
  width: 100%;
  background: rgba(0, 0, 0, 0.3);
}
.footer-grid{
  position: relative;
  width: 100%;
  height:200px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  align-content: space-between;
}
.bottonA-wrap{
  display: inline-block;
  text-align: center;
}
.bottonA{
  justify-self: start;
  align-self : center;
  & h3{
    font-family: Comfortaa;
    font-size: 20px;
    padding: 10px;
    font-weight: 600;
    letter-spacing : 0.2em;
    color: $col-rozW;
  }
  & span{
    font-size: 14px;
    text-transform: uppercase;
  }
}
.footer-grid .bottonB{
  text-align: center;
  align-self : center;
  & strong{
    font-size: 26px;
  }
} 
.bottonC{
  display: grid;
  align-self :center;
  & .botton-icon{
    justify-items: end;
    display: inline-block ;
  }
}
.menu-footer{
  position: absolute;
  top: 0%;
  left:50% ;
  margin-left:-330px;
  padding: 15px 0;
  z-index: 2 ;
  display: none;
  & ul>li{
    padding: 7px 5px;
    display: inline-block;
  }
}

@media only screen and (max-width:768px) {
  .menu-footer{
    position: absolute;
    top :-420px ;
    margin-left:-100px;
    width: 200px;
    height: auto;
    padding: 20px 0 30px;
    text-align: center;
    background-color:rgba($color:$col-btn, $alpha: 0.8);
    z-index: 9;
     & ul>li{
      display:block;
    }}
    .footer-grid{
      grid-template-columns: 1fr 1fr;
      grid-template-rows:1fr auto;
      align-content: stretch;
    }
    .bottonA{
      grid-column: 1/2;
      grid-row: 1/2;
      align-self :end;
    }
    .bottonC{
      grid-column: 2/3;
      grid-row: 1/2;
      align-self :end;
      padding-bottom: 10px;
    }
    .footer-grid .bottonB{
      grid-column: 1/3;
      grid-row: 2/3;
      font-size: 14px;
      padding-bottom: 45px;
      & strong{
        font-size: 20px;
      }
    }
}
@media only screen and (max-width:450px) {
  .footer-grid{
    height: 200px;
    padding-bottom: 30px;
    grid-template-columns: 1fr ;
    grid-template-rows:auto auto auto;
    align-content :stretch ;

  .bottonA {
    display: block;
    grid-column: 1/2;
    grid-row: 2/3; 
    transform: translateY(-30px);
    align-self :start; 
    justify-self :center;
  }
  .bottonC{
    grid-column: 1/2;
    grid-row: 1/2;
    align-self :start;
    padding-top: 20px;
  }
  .bottonB{
    grid-column: 1/2;
    grid-row: 3/4;
    align-self :end;
    font-size: 14px;
    padding-bottom: 20px;
  }
  .bottonA-wrap{
    & h3{
      padding-top: 20px;
    }
  }
}
}