.saze{
  margin-top: 20px;
}
.saze-flex{
  width: 100%;
  height: auto;
  display:flex ;
  flex-wrap :wrap;
  flex-grow : 1;
  justify-content: center;
  text-align: center;
  & span{
    font-family: Arial , sans-serif;
    font-size: 32px;
    font-weight: 800;
    padding: 5px 10px 3px;
    margin: 0 10px 10px;
    text-transform: uppercase;
    color:lighten($col-btn, 15%);  
    border-bottom: #444444 2px solid;
    border-top: transparent 2px solid;
    border-radius: 5px;
    cursor: pointer;
    transition: all .3s ease;
    &:hover{
      color:  #7d0f17;
      transform: scale(1.1);
      border-top: #fff 2px solid;
      box-shadow: 3px 6px 3px #444;
    }
  }
}

.level11,.level12,.level13,.level14,.level14-2{
  display: none;
}
.uzer-saze{
  display: none;
  padding: 20px 0;
  @include uz;
  left: 0;
  top: 0;
  & h4{
    white-space : nowrap; 
    word-spacing : 5px; 
    padding: 5px 20px;
  }
}
.rezul{
  display: none;
}
#sz{
  font-size: 16px;
}
#sz2{
  font-family: serif;
  font-size: 26px;
}
.close-uz-saze{
  @include uz-close;
  top: 5px;
  left: 5px;
}


@media only screen and (max-width:450px) {
  .uzer-saze{
    width: 100%;
    margin: 0px;
    border-radius: 0;
    padding:  10px 20px;
    font-size: 16px;
    background-color:rgba($color: #520a0f, $alpha: 0.8) ;
    & h4{
      font-size: 18px;
      padding: 0;
    } 
  }
  .close-uz-saze{
    color: #ffffff;
    opacity: .6;
    left: 10px;
    &:hover{
      opacity: 1;
    }
  }
  .saze-flex span{
    font-size: 26px;
  } 
  .level11,.level12,.level13,.level14{
    margin: 0 15px 0 30px;
    & form > span{
      font-size: 16px; 
      line-height: 1.5;
    }
  }
}