
.arrow {
  cursor: pointer;
  font-size: 26px;
  background-color: $col-roz;
  color: $col-btn;
  outline: none;
  border: none;
  border-radius: 50%;
  padding: 5px 10px;
  z-index: 5;
  transition: all .3s ease;
  &:hover{
    background-color: $col-btn;
    color: $col-w;
  }
}
.prev {
  position: absolute;
  top: 15%;
  right: 3%;
}
.next {
  position: absolute;
  top: 15%;
  left: 3%;
}
.cont-slik {
  margin: 30px 0 100px;
 & div{
    text-align: center;
    margin: 10px 25px 15px;
     &  img{
      margin: 0 auto 20px;
      height: 50px;
      border-radius: 5px;
     }
    }
  }


  @media only screen and (max-width:450px) {

    .cont-slik {
      margin: 50px 0 10px;
      & div{
        margin-bottom: 0px;
      }
     & img{
      margin-bottom: 20px;
      }
}

  }