@mixin btnAp {
	position: fixed;
	width: 50px;
	height: 50px;	
  right: -100px;
	border-radius: 50%;
	text-align: center;
	color: $col-roz;
	background-color: $col-btn;
	opacity: .6;
	z-index: 10;
	cursor: pointer;
	transition: all .25s ease;
	&:hover{
		opacity: 1;
	}
	&.act{
		right: 20px;
	}
}

.heder {
  overflow:hidden;
  position: relative;
  height: 100vh;
  width: 100%;
  color: $col-w;
  background:  50% 95% /cover;
}

.heder-wrap {
  position: absolute;
  height: 100vh;
  width: 100%;
  background: rgba(0, 0, 0, 0.3);
}

.wrap {
  width: 90%;
  margin: 0 auto;
}

.heder-grid {
  width: 100%;
  height: 100vh;
  display: grid;
  align-content: space-between;
}

.line-top {
  padding-top: 2%;
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: 1fr;
}
.logo-wrap{
  display:grid ;
  grid-template-columns: auto 1fr;
  & a{
    display: none;
  }
}
.logo-wrap .logo {
  display: inline-block;
  font-size: 26px;
  font-family: Cormorant Infant, Monotype Corsiva;
}

.menu-top {
  padding: 7px 0;
  display: inline-block;
  justify-self: end;
}

.uaTop{
  position: absolute;
  top:10%;
  right: 5%;
  & a{
    text-decoration: none;
    padding-left: 10px;
    color:#fff;
  }
}
.uaPage {
  text-align:right;  
  & a{
    text-decoration: none;
    padding-left: 10px;      
    color:#fff;
  }
}
.ru:lang(ru), .ua:lang(uk){
  font-weight: 700;
}

.heder-h1 {
  width: 100%;
  display: inline-block;
  text-align: center;
  text-transform: uppercase;
  font-size: 18px;
  word-spacing: 10px;
  transform: translateY(-30px);
  & .h1 {
    width: 100%;
    font-family: Comfortaa;
    font-size: 80px;
    font-weight: 600;
    padding: 15px 0;
    & span{
      display: inline-block;
      transform:scale(1.4) ;
      color: $col-rozW;
   }
  } 
  .h1-text-top,.h1-text-botton{
    position: relative;
    display: inline-block;
    width: 100%;
    height: 50px;
    overflow: hidden;

    & #h1-top{ //
      position: absolute;
      width: 100%;
      height: 50px;
      padding-top: 15px;
    }
    & #h1-botton{ //
      position: absolute;
      width: 100%;
      height: 50px;
      padding-top: 15px;
    }
  }
}
@supports (-webkit-overflow-scrolling: touch) {
  .heder-h1 .h1{
    margin-left: -14%;
  }
}
.hed-line-botton {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 4%;
  font-size: 18px;
  align-self: center;

  & .hed-home {
    padding-top: 3px;
  }

  & .hed-phone {
    justify-self: end;

    & strong {
      font-size: 22px;
      font-family: serif;
    }
  }
}
// -----------menu icon -----------

.toggle-mnu {
  justify-self :end;
  align-self :center;

  display: block;
  width: 28px;
  height: 28px;

  & span:after, span:before {
    content: "";
    position: absolute;
    left: 0;
    top: 9px;
  }
  & span:after{
    top: 18px;
  }
  & span {
    position: relative;
    display: block;
  }
  & span, span:after, span:before {
    width: 100%;
    height: 2px;
    background-color: #fff;
    transition: all 0.3s ease;
    backface-visibility: hidden;
    border-radius: 2px;
  }

  &.on span {
    background-color: transparent;
  }
  &.on span:before {
    transform: rotate(45deg) translate(-1px, 0px);
  }
  &.on span:after {
    transform: rotate(-45deg) translate(6px, -7px);
  }
}
//--всплываыщие подсказки---

.pull,.pullA,.pullB {
  opacity:0 ;
  overflow:hidden;
  position: absolute;
  display: flex;
  width: 400px;
  height: 200px;
  z-index: 5;
  top: 50%;
  left: 50%;
  margin-top: -100px;
  margin-left: -200px;
  background-color: lighten($col-rozW, 9) ;
  border: $col-w 3px solid;
  box-shadow: $col-roz 0 0 0 2px ,  inset  $col-roz 0 0 0 4px ;
  border-radius: 50%;
  & span{
    margin: auto ;
    & >p{
      font-weight: 600;
      line-height: 1.2;
      font-size: 22px;
      padding: 0 10%;
      text-align: center;    
      color:rgba($color: #000000, $alpha: 0.8);
    }
  }
 }
 .pullA span{
   padding-bottom: 20px;
 }
// -------Мышка внизу экрана-------
.mous-wrap{
  position:absolute ;
  width: 60px;
  height: 60px;
  top: 80%;
  left: 50%;
  margin: -20px;
  z-index:6;
  & h3{
    font-size: 18px;
    margin-left: -45px;
    font-weight: 400;
    opacity: .9;
  }
}
.mouse-icon {
  border: 3px solid #fff;
  border-radius: 16px;
  height: 40px;
  width: 24px;
  margin: auto;
  display:block;
  z-index: 10;
  opacity: 0.7;
  cursor: pointer;
}
.mouse-icon .wheel {
  animation-name: drop;
  animation-duration: 1s;
  animation-timing-function: linear;
  animation-delay: 0s;
  animation-iteration-count: infinite;
  animation-play-state: running;
}
.mouse-icon .wheel {
  position: relative;
  border-radius: 10px;
  background:  #fff;
  width: 3px;
  height: 6px;
  top: 4px;
  margin-left: auto;
  margin-right: auto;
}
@-webkit-keyframes drop {
  0% {
    top: 5px;
    opacity: 0;
  }
  30% {
    top: 10px;
    opacity: 1;
  }
  100% {
    top: 25px;
    opacity: 0;
  }
}
@keyframes drop {
  0% {
    top: 5px;
    opacity: 0;
  }
  30% {
    top: 10px;
    opacity: 1;
  }
  100% {
    top: 25px;
    opacity: 0;
  }
}
// ----------------кнопка наверх -------------

.btn-up{
  position: relative;
  @include btnAp;
  font-size: 36px;
  bottom: 20px;
  & span{
    position: absolute;
    font-size: 26px;
    transform:translate(-26px, 15px);
  }
}
//---------------media-heder--------------

@media only screen and (max-width:1100px) {
  // menu
  .line-top {
    grid-template-columns:1fr ;
    grid-template-rows: auto auto;
  }
  .logo-wrap{
    & a{
      display:block;
    }
  }
  .menu-top{  
    display: none;
    position: absolute;
    left:50% ;
    top:100px ;
    margin-left:-100px;
    width: 200px;
    height: auto;
    padding: 20px 0 30px;
    text-align: center;
    background-color:rgba($color:$col-btn, $alpha: 0.8); 
    z-index: 9;
  }
 .heder-h1 .h1{
    font-size: 70px;
  }
}

@media only screen and (max-width:930px) {
  .heder-h1 .h1{
