  }
  .hed-line-botton {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
    font-size: 16px;
    padding-bottom: 10px;
    & span {
      justify-self: center;
      padding: 0px ;
    }
    & .hed-phone {
