nav ul {
  display: inline-block;
  padding-left: 0;
  margin: 0;

  &>li {
    font-size: 16px;
    padding: 0 5px;
    display: inline-block;
    list-style-type: none;
    position: relative;

    & ::before {
      content: ' ';
      position: absolute;
      bottom: -2px;
      width: 0.1%;
      height: 1.5px;
      left: 50%;
      margin-left: -1%;
      background-color: #ffffff;
      transition: all 0.3s ease;
    }

    &:hover ::before {
      width: 80%;
      left: 50%;
      margin-left: -40%;
      background-color: $col-w;
    }

    & a {
      text-decoration: none;
      color: $col-w;
    }

  }
}

.active {
  border-bottom: $col-w 2px solid;
}

.submenu,
.menu-top {
  padding-left: 0;
}


@media only screen and (max-width:1100px) {
  nav ul > li {
    display: block;
    margin: 3px auto;
    text-align:center;
    padding :5px 20px;
  }
}