@mixin uz {
  position: fixed;
  z-index: 99;
  margin: 10px;
  padding:  25px;
  width: 200px;
  height: auto;
  border-radius: 50%;
  background-color:rgba($color: #520a0f, $alpha: 0.8) ;
  color: $col-w;
  font-size: 20px;
  word-spacing : 10px;
  font-weight:300;
  text-align: center;
  & p{
    word-spacing : 5px;
    font-size: 16px;
  }
}
@mixin uz-close {
  position: fixed;
  cursor: pointer;
  font-size: 30px; 
  color:$col-btn; 
}
@mixin btn{
  width:auto ;
  height:40px;
  align-self : center;
  background-color: lighten($col-rozW, 6) ;
  outline: none;
  color: $col-btn;
  border: $col-Troz 3px solid;
  border-radius: 20px;
  font-size: 18px;
  white-space : nowrap; 
  padding: 5px 15px;
  transition: color .5s ease;
  &:hover{
    cursor: pointer;
    border: $col-btn 3px solid;
    background-color: $col-btn;
    transition: color .5s ease;
    color: $col-w;
  }
}

.s-conteiner{
  min-height: 400px;
  padding: 0 0 100px 0;
}

article>p{
  padding-left: 20px;
  font-size: 20px;
  text-indent :30px; 
}
.uzer-klubok{
  display: none;
  @include uz;
  right: 0;
  top: 0;
  & h5{
    margin-left: -10px;
    white-space : nowrap; 
    word-spacing : 5px; 
    padding: 5px 20px;
  }
}
.close-uz-klubok{
  @include uz-close;
  top: 5px;
  right: 5px;
}
.firm{
    width: 100%;
    margin: 10px 0 40px;
    height: auto;
    display:flex ;
    justify-content: center;
    & .infirm{
      height: 70px;
      outline: none;
      margin: 10px 15px;
      border-bottom: #444444 2px solid;
      border-radius: 5px;
      transition: all .3s ease;
       &:hover{
        transform: scale(1.1);
        box-shadow: 3px 6px 3px #444444 ;
      }

  }
  & input{
    padding: 5px 10px;
  }
  & input:nth-child(3){
    padding:5px 20px ;
    background-color:$col-btn;
  }
}

// -----level2--------
.level2{  display: none;}
.level3{  display: none;}
.level4-1{  display: none;}
.level4-2{  display: none;}
.level4-3{  display: none;}
.level5{  display: none;}
.level6{  display: none;}
h3{
  letter-spacing :.15em;
  font-weight: 600;
  font-size: 20px;
  text-transform: uppercase;
  text-align: center;
  padding:20px 0 ;
}
form>input{
  transform: translateY(8px);
  width: 30px;
  height: 30px;
  margin:5px 30px; 
}
form>span{
  line-height: 1.8;
  font-size: 18px;
  padding: 15px 0 30px;
  & strong{
    padding-right: 15px;
  }
}
// ---------------level7 collectyons------------
.level7{  display: none;}
#Erorr{
  display: inline-block;
  margin:  40px 0;
  font-size: 20px;
  text-align: center;
  & strong{
    cursor: pointer;
    position: absolute;
    right: 0;
    top: 70%;
    color: #000;
  }
}
.collectyons{
  display: none; 
  width: 100%;
  text-align: center;
  height: auto;
  & .coll-wrap{
    width: 100%;
    height:auto;
    text-align: center;
    margin-bottom: 30px;
    & h4{
      cursor: pointer;
      display: inline-block;
      font-size: 22px;
      padding: 5px 20px 7px;
      margin: 10px 0;
      border-bottom: #444444 1px solid;
      border-radius: 5px;
      text-decoration-line: underline;
      transition: all .3s ease;
      background-color: lighten( $col-rozW , 6%);
      &:hover{
        background-color:lighten( $col-btn , 6%);
        color: $col-w;
      }
    }
    .coll-flex{
      display: grid;
      grid-template-columns: auto auto 170px;
      grid-gap: 10px;
      justify-content : space-between ; 
      background-color: lighten($col-rozW, 6) ;
      padding: 10px 30px; 
      & img{
        height: 100px;
        border-radius:5px;
        transition: all 0.5s ease;
        &:hover{
          cursor: pointer;
          object-fit : cover ;
          transition: all 0.5s ease;
          transform: scale(3);
          border: $col-roz 1px solid;
        }
      }
      & span{
        min-width: 200px;
        font-size: 20px;
        word-spacing : 10px;
        align-self : center;
        text-align: center;
      }
      & button{
        @include btn;
      }
    }
  }
  .coll-collor{
    display: none;
    margin: 20px 0;
    width: 100%;
    text-align: center;
    & img{
      height: auto;
      display: inline-block;
    }
  }
   .coll-alt{
    & .coll-alt0,.coll-alt1,.coll-alt2,.coll-alt3{
      @include btn;
      margin: 10px 5px;
    }
  }
   .coll-btn-stop{
    @include btn;
    width: 100%;
    margin-top: 20px;
    padding: 5px 25px;
  }
}



@media only screen and (max-width:970px) {
  .s-wrap{
    width: 90%;
    .collectyons .coll-collor img {
      width: 100%;
    }
  }
  .firm {
    flex-wrap :wrap;
    flex-grow : 1;
  }

}

@media only screen and (max-width:870px) {
  .collectyons .coll-wrap .coll-flex{
    grid-template-columns: 1fr;
    grid-template-rows: auto auto auto;
    text-align: center;
    & img{
      display: inline-block;
      margin: 10px auto;
     }
    & button{
      display: inline-block;
      margin: 10px auto;
    }
  }
  .collectyons .coll-wrap{
    background-color: lighten( $col-rozW , 6%);
    & h4{
      background-color:lighten( $col-btn , 10%);
      text-decoration-line: underline;
      transition: color .3s ease;
      color: $col-w;
      border:none;
      margin-bottom: 0;
    }
  }
}

@media only screen and (max-width:670px) {
#Erorr{
  & strong{
    right: 0;
    top: 74%;
  }
}

}

@media only screen and (max-width:450px) {
  form>input{
    margin-left:5px; 
    margin-right: 15px;
  }
  .uzer-klubok{
    width: 100%;
    margin: 0px;
    border-radius: 0;
    padding:  10px 30px;
    font-size: 16px;
    & h5{
      font-size: 18px;
      padding: 0;
    }
  }
  .close-uz-klubok{
    color: #ffffff;
    opacity: .6;
    &:hover{
      opacity: 1;
    }
  }
  .firm .infirm{
      height: 60px;
  }

  .level4-1,.level4-2,.level4-3,.level2,.level3,.level5,.level6{
    margin: 0 15px 0 30px;
    & form > span{
      font-size: 16px; 
      line-height: 1.5;
    }
  }
  #Erorr{
    & strong{
      right: 0;
      top: 75%;
    }
  }
   .coll-btn-stop{
    & span{
      display: none;
    }
  }
   .coll-alt{
    & .coll-alt0,.coll-alt1,.coll-alt2,.coll-alt3{
      margin: 5px 5px;
      font-family: 14px;
    }
  }

}