
@import "base_css/_base" ;
@import "base_css/fons";
@import "base_css/pullAnim";
@import "base_css/slick";
@import "base_css/slick-theme";
@import "word_css/heder","word_css/_heder-page";
@import "word_css/footter","word_css/footer-page";
@import "word_css/_nav";
@import "word_css/about";
@import "word_css/blog";
@import "word_css/s-klubok","word_css/s_saze","word_css/s-model","word_css/s-yxod","word_css/s-review","word_css/s-contact";

*{
  padding: 0;
  margin:0 ;
  box-sizing : border-box ;
}

body{
  width: 100%;
  font-size: 15px;
  font-family: Roboto, Arial, Helvetica, sans-serif;

}

//сейчаc
.ff{
   background-position : 50% 20%;
   background-attachment : fixed ;
 } 
/* портретная ориентация */
@media screen and (orientation:portrait) {
.ff{
  background-size: auto 100%;
  background-position : 50% 50%;
  background-repeat: no-repeat;
  }
}
/* горизонтальная ориентация */
@media screen and (orientation:landscape) {
.ff{
  background-size: 100%  auto;
}
}

.verena{
  position: absolute;
  padding: 20px 20px;;
  top:  350px;
  left: 0%;
  font-size: 16px;
  color: $col-w;
  opacity: .6;
}



#ff1{
    background-image: url(/app/img/paral11.jpg);
}
#ff2{
  background-image: url(/app/img/paral14.jpg);
}
#ff3{
  background-image: url(/app/img/paral16.jpg);
}
#ff4{
  background-image: url(/app/img/paral18.jpg);
}
#ff5{
  background-image: url(/app/img/paral21.jpg);
}


//=========для айфона=============

@supports (-webkit-overflow-scrolling: touch) {
  /* CSS specific to iOS devices */ 
      .ff{
        background-size: 100%;
      background-attachment : scroll ;
    }
}


section{
  width: 100%;
  min-height: 400px;
  position: relative;
  overflow: hidden;
  background-color: lighten($col-rozW, 9) ;
  & h2{
    padding: 40px 0;
    text-align: center;
    color: $col-h2;
    font-weight: 600;
    text-transform: uppercase;
  }
}
.s-wrap{
  width: 80%;
  position: relative;
  margin: auto;
}
.s-conteiner article p strong{
  font-size: 26px;
  color: $col-btn;
}
.rel{
  display: none;
}


@media only screen and (max-width:670px) {
  .s-wrap{
    width: 90%;
  }
}

@media only screen and (max-width:450px) {
  section h2{
    padding: 40px 0 30px;
  }
  section  article p{
    font-size: 16px; 
    line-height: 1.5;
  }
  .s-conteiner article p strong{
    font-size: 20px;
  }
  .verena{
    padding: 5px 5px;
  }
  #model , #review , #ff2 , #ff5, #model-ua , #review-ua {
    display: none;
  }
}

