.footer2{
  height: 20vh;
  width: 100%;
  color: $col-w;
  background: url('/app/img/a1.jpg') 50% 45% /cover;
}
.footer-wrap2{
  position: absolute;
  height: 20vh;
  width: 100%;
  background: rgba(0, 0, 0, 0.3);
}
.footer-grid2{
  position: relative;
  width: 100%;
  height:20vh;
  font-size: 18px;

  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  align-content: space-between;
}
.bottonA-wrap2{
  display: inline-block;
  text-align: center;
}
.bottonA2{
  justify-self: start;
  align-self : center;
  & h3{
    font-family: Comfortaa;
    font-size: 20px;
    font-weight: 600;
    letter-spacing : 0.2em;
    color: $col-rozW;
    padding: 10px 0;
  }
  & span{
    font-size: 14px;
    text-transform: uppercase;
  }
}
.foot-home{
  align-self: center;
  justify-self : center;
}
.foot-phone{
  align-self: center;
  justify-self : end;
  & strong{
    padding-right: 15px;
    font-family: serif;
  }
} 


@media only screen and (max-width:1140px) {
  .footer-grid2{
    grid-template-columns: 1fr auto 1fr; 
  }
.foot-home{
  display:none;
}
.foot-phone{
  grid-column: 3/4;
}
}

@media only screen and (max-width:570px) {
  .footer2{
    height: 20vh;
  }
  .footer-wrap2{
    height: 20vh;
  }
  .footer-grid2{
    height: 20vh;
    grid-template-columns: 1fr ; 
    grid-template-rows: auto auto;
  }
.bottonA-wrap2{
  margin-top: 10px;
} 
.bottonA2{
    justify-self:center;
    align-self : end;
    & h3{
      padding: 10px 0 0;
    }
  }
  .foot-phone{
    grid-column: 1/2;
    grid-row: 2/3;
    justify-self : center;
    padding-bottom: 15px;
    & strong{
      padding-right:0px;
    }
  }
  #fphon{
    display: none;
  }

}








