@mixin teg {
  padding: 5px 10px;
  border-radius: 3px;
  background-color: $col-Troz;
  font-size: 15px;
  transition: all .3s ease;
  & a{
    text-decoration : none;
    transition: all .3s ease;
      color: $col-w;
  }
  &:hover{
    background-color: $col-btn;
    color: $col-w;
    }
}


#s-blog{
  width: 100%;
  display: flex;
  padding: 40px 0 80px;
  flex-wrap: wrap;
}
.blog-p{
  width: 70%;
}
.post{
  width: 100%;
  display: grid;
  grid-template-columns: 200px auto;
  grid-column-gap: 20px;
  margin-bottom: 20px;
  padding: 10px 15px;
  justify-content :space-between ;
  & .poster{
    height: 266px;
    border-radius: 20px;
  }
}
.post-preview{
  position: relative;
  width: auto;
  height: auto;
  text-align: right;
  font-size: 20px;
}
.post-preview .blog-h2{
  display: inline-block;
  margin-bottom: 15px;
  padding: 5px 15px;
  background-color: lighten($col-rozW, 6);
  border-top: transparent 2px solid;
  border-bottom: #444444 2px solid;
  border-radius: 5px;
  transition: all .3s ease;
  cursor: pointer;
  & a{
    font-size: 26px;
    color: $col-btn;
    text-decoration : none;
    }
    &:hover{
      border-top: #fff 2px solid;
      transform: scale(1.1);
      box-shadow: 3px 6px 3px #444444 ;
    }
}
.date{
  text-align: right;
  padding: 10px 15p 0;
}
.post-preview .meta{
  display: inline-block;
  margin: 5px 3px 10px;
  @include teg;
}


.post-wrap:nth-child(2n) .post{ 
  background-color: lighten($col-rozW, 2);
  display: grid;
  grid-template-columns: auto 200px;
  grid-template-rows: 1fr;
  & .poster {
    grid-column: 2/3;
  }
    & .post-preview {
      grid-row: 1/2;
      grid-column: 1/2;
      text-align:left;
      & .date{
        text-align: left;
      }
    }
}
.date{
  margin: 20px 0px;;
}

.widgets{
  text-align: center;
  width: 30%;
  padding: 0 0px 0 40px;
  order: 0;
  & .tag-list .tag-item{
    display: inline-block;
    margin: 5px 3px;
    @include teg;
  }
  & .archive{
    margin-top: 30px;
    font-size: 18px;
    & ul{
      list-style: none; 
      margin: 5px 0 10px;
      & li a{
        color: $col-btn;
      }
    }
  }
}
.widgets .archive ul li a:hover{
  color: #000;
}
// --------------tegs-------------------
.tags{
  padding-bottom: 80px;
}
.tags .tag-li ul li {
  text-align: center;
  margin-bottom: 20px;
  & a{
    padding: 5px 15px;
    border-top: transparent 2px solid;
    border-bottom: #444444 2px solid;
    border-radius: 5px;
    font-size: 16px;
    color: $col-btn;
    text-decoration : none;
    transition: all .3s ease;
    &:hover{
      border-top: #fff 2px solid;
      border-bottom: #444444 2px solid;
      transform: scale(1.1);
      box-shadow: 3px 6px 3px #444444 ;
    }
    }
}
.bbt{
  width: 100%;
  text-align: center;
} 
.post-btn{
  background-color: lighten($col-rozW, 6) ;
  outline: none;
  color: $col-btn;
  border: $col-Troz 3px solid;
  border-radius: 20px;
  font-size: 18px;
  white-space : nowrap; 
  padding: 5px 15px ;
  margin-top: 15px;
  transition: color .5s ease;
  &:hover{
    cursor: pointer;
    border: $col-btn 3px solid;
    background-color: $col-btn;
    transition: color .5s ease;
    color: $col-w;
  }
}
#arx{
  display: none;
}
// --------------posts---------------------

.article article{
  text-align: center;
  font-size: 20px;
  text-indent :30px;
  .post-h2{
    padding-bottom: 20px;
  }
  & .post-date{
    text-align: right;
  }
  & .exp{
    display: none;
  }
  & :nth-child(4){
    display: none;
  }
  & .img-post{
    display: block;
    margin: 0 auto;
    width: 60%;
    margin-bottom: 20px;
  }
  & p{
    text-align: left;
    padding-left: 20px;
    padding-bottom: 20px;
  }
  & strong{
    font-size: 16px;
  }
}

.Previous-next{
  position: relative;
  display: block;
  width: 100%;
  margin-top: 40px;
  font-weight: 500;
  .p_n{
    height: 20px;
    color: $col-Troz;
    text-decoration : none;
    transition: all .3s ease;
    display: inline-block;
    text-align: center;
    padding: 5px 25px 5px 0px;;
    &:hover{
      color: $col-btn;
    }
  }
  .previous{
    margin-left: 20px; 
   }
  .nextous{
    margin-left: -30px;
  }
}
#exit-blog{
  height: 20px;
  float: left;
  color: $col-Troz;
  transition: all .3s ease;
  text-decoration: none;
  padding: 5px 25px 5px 0px;;
  &:hover{
    color: $col-btn;
  }
  & b{
    padding-right: 7px;
  }
}
#exit-cont{
  color: $col-Troz;
  transition: all .3s ease;
  &:hover{
    color: $col-btn;
  }
}
#exit-constr{
  position: fixed;
  height: 30px;
  color:#fff;
  background-color: lighten($col-btn, 6) ;
  left: -300px;
  top: 30px;
  padding-right: 15px;
  & a{
    color:#fff;
    padding: 15px 17px;
  }
  & b{
    margin-left: -10px;
  }
}




@media only screen and (max-width:1100px) {
  .widgets{
    width: 100%;
    padding: 0;
    order: -1;
    & .tag-list {
      white-space: nowrap;
      overflow-x: scroll;
      -ms-overflow-style: none;
     & .tag-item {
      margin-bottom: 15px;
      }
     }
     .blog .widgets .tag-list::-webkit-scrollbar {
        width: 0; }
    & .archive{
      display: none;
    }
  }
  .blog-p{
    width: 100%;
    margin-top: 20px;
  }
  .post-preview {
    font-size: 16px;
  & .blog-h2 a{
    font-size: 20px;
  }}
}

@media only screen and (max-width:660px) {
  #s-blog{
    padding-top: 10px;
    padding-bottom: 40px;
  }
  .post-wrap:nth-child(2n) .post{ 
    padding-bottom: 25px;
    grid-template-columns:  auto 28%;
    grid-column-gap: 10px;
    padding-left: 05px;
    padding-right: 5px;
    & .post-preview{
      margin-left: 0;
    }
    & .poster {
    margin: 0px;
    }
  }
  .post{
    grid-template-columns: 25% auto;
    grid-column-gap: 15px;
     & img{
    margin: 15px auto 10px;
    }
    & .post-preview {
      width: 100%;
      margin-left: 15px;
      & .blog-h2{
        padding: 0;
        line-height: 1.2;
        margin-bottom: 5px;
        padding-bottom: 7px;
        border-bottom: #999 2px solid;
        &:hover{
          border-bottom: #444444 2px solid;
        }
      }
      & .blog-h2 a{
        padding: 0 15px;
        font-size: 14px;
        text-decoration-line: underline;
      }
      & .meta{
        display: none;
      }
    }
  }
   .post .poster{
     width: 25vw;
     height: auto;
     border-radius:5px;
    }
  .article article{
    & .img-post{
      width: 80%;
    }
    & .post-date{
      padding: 0px 0 20px ;
    }
  }
  #exit-blog{
    font-size: 18px;
    margin-left: -15px;
  }
  .Previous-next{
    display: none;
  }
  .date{
    font-size: 12px;
    margin-top: 5px;
    margin-bottom: 10px;
  }
}

@media only screen and (max-width:420px) {
  .date{
    font-size: 10px;
  }
  #exit-blog{
    font-size: 16px;
    margin-left: -25px;
  }
  #exit-constr{
    top: 0px;
    & a{
      padding: 5px 5px;
    }
  }
    // ===================
    .post-wrap:nth-child(2n) .post{ 
      padding-bottom: 0;
      padding-top: 10px;
      padding-right: 5px;
      margin-bottom: 10px;
      & .poster{
        margin-top: 0;
        margin-bottom: 10px;
      }
      & .post-preview .blog-h2{
        padding: 5px 0 7px;
        margin-bottom: 5px;
        margin-right: 5px;
      }
    }
    .post-wrap .post{
      padding: 0px ;
      & .poster{
        margin-top: 0;
      }
    }
    .post .post-preview{
      margin-left: 0;
      font-size: 12px;
      & .blog-h2{
        padding: 5px 0 7px;
        & a{
          font-size: 14px;
        }
    }
  }
}

