@mixin form {
  outline: none;
  color: $col-btn;
  background-color: lighten($col-rozW, 6) ;
  border: $col-roz 3px solid;
  border-radius: 5px;
  padding: 10px 15px;
  margin: 10px 0 15px;
  &:focus{
    background-color:$col-w ;
    box-shadow: $col-roz 0 0 10px ;
  }
}

iframe {
  width: 80%;
  // height :330px;
  height: 400px;
  text-align: center; 
  @include form;
  overflow-x: hidden;
  margin: 15px 0 0 10%;
}

#formaction , address{
  display: block;
  width: 100%;
  font-size: 20px;
  padding: 20px 0  0; 
  text-align: center; 
  & textarea{
    width: 60%;
    @include form;
    font-size: 24px;
    margin: 15px 20% 0px;
  }
  & button{ 
    @include btn;
    margin: 20px 0 10px;
  }
}
.form-name{
  position: relative;
  display:grid;
  width: 60%;
  margin: 0 auto;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
  & input {
    width: 100%;
    @include form;
    font-size: 18px;
    margin: 0px; 
  }
}
#stok{
  position: absolute;
  right: 0;
  bottom: 20px;
  font-size: 12px;
  & a{
    color: $col-Troz;
    &:hover{
      color: $col-btn;
    }
  }
 }

@media only screen and (max-width:1100px) {
  #formaction textarea{
      width: 80%;
      @include form;
      font-size: 20px;
      margin: 15px 10% 0px;
    }
    .form-name{
      width: 80%;
    }
}
@media only screen and (max-width:820px) {
  #formaction textarea{
      width: 100%;
      @include form;
      font-size: 20px;
      margin: 15px 0% 0px;
    }
    .form-name{
      width: 100%;
    }
}
@media only screen and (max-width:640px) {
  .form-name{
    display:block;
    & input {
      margin: 5px 0;
    }
  }
  #formaction textarea{
    padding-left: 3px;
  }
  iframe {
    width: 90%;
    height :330px;
    margin-left: 5%;
  }
}
@media only screen and (max-width:450px) {
  address{
    font-size: 18px;
  }
  iframe {
    width: 100%;
    height :330px;
    margin-left: 0;
  }
}